// Custom media query range
@mixin mq($min, $max) {
  @media only screen and (min-width: $min+'px') and (max-width: $max+'px') {
    @content;
  }
}

// Clearfix
@mixin clearfix {
  &:after {
    content: "";
    display: table;
    clear: both;
  }
}

// Center vertically
@mixin centerY {
  position: relative;
  top: 50%;
  transform: (translateY(-50%));
}

// Center both vertically and horizontally
@mixin centerXY {
  position: relative;
  top: 50%;
  left: 50%;
  transform: (translateY(-50%) translateX(-50%));
}




